import React, { Suspense } from "react";
import { Button, CardKelas } from "../../../component";
const Hero = React.lazy(() => import("../../../component/Hero"));
const ChangeKelas = React.lazy(() => import("../../../component/ChangeKelas"));
const About = React.lazy(() => import("../../../component/About"));

const Pertanian = () => {
  const PertanianItem = [
    {
      name: "Budidaya Cabai",
      mentor: "Tholib",
      background: "./assets/img/changekelas/pertanian/budidaya-cabai.png",
      to: "/budidaya-cabai",
      disable: true,
    },
    {
      name: "Budidaya Jagung",
      mentor: "Tholib",
      background: "./assets/img/changekelas/pertanian/budidaya-jagung.png",
      to: "/budidaya-jagung",
      disable: true,
    },
    {
      name: "Budidaya Kacang Panjang",
      mentor: "Tholib",
      background:
        "./assets/img/changekelas/pertanian/budidaya-kacang-panjang.png",
      to: "/budidaya-kacang-panjang",
      disable: true,
    },
    {
      name: "Budidaya Kacang Tanah",
      mentor: "Tholib",
      background:
        "./assets/img/changekelas/pertanian/budidaya-kacang-tanah.png",
      to: "/budidaya-kacang-tanah",
      disable: true,
    },
    {
      name: "Budidaya Padi",
      mentor: "Tholib",
      background: "./assets/img/changekelas/pertanian/budidaya-padi.png",
      to: "/budidaya-padi",
      disable: true,
    },
  ];
  return (
    <main>
      <Suspense fallback={<div>Loading...</div>}>
        <Hero page="pertanian">
          <Button
            color="primary"
            bgcolor="bgsecondary"
            onClick={() => window.location.replace("/pertanian#changekelas")}
          >
            Lihat Daftar Kelas
          </Button>
        </Hero>
        <About page="pertanian">
          <Button color="primary" styles="outline">
            Pelajari Lebih Lanjut
          </Button>
        </About>
        <ChangeKelas id={"changekelas"}>
          {PertanianItem.map((item) => {
            return (
              <CardKelas
                bgImg={item.background}
                disable={item.disable}
                url={item.to}
                mentor={item.mentor}
              >
                {item.name}
              </CardKelas>
            );
          })}
        </ChangeKelas>
      </Suspense>
    </main>
  );
};

export default Pertanian;
